import React from "react"
import './css/style_brand.css'
import './css/style_vot.css'
import search from './img/img_vot/Vector.png'
import smile from './img/img_vot/Vector (Stroke).png'
import like from './img/img_vot/Vector 348 (Stroke).png'
import dis from './img/img_vot/Vector (Stroke)smile.png'
import{Link} from "react-router-dom";


class Nav extends React.Component{
    render(){
        return(
            <div className="nav_right">
            <div className="search_main">
                <div className="input_size">
            <input onChange={this.props.handleChange}  type="text" className="search_dog" placeholder="Search for breeds"/>
            <button onClick={this.props.handleSubmit} className="search_but"><img src={search}className="search_size"/></button>
            </div>
        </div>
            <Link to="/like" className="size_a"><div className="smile_nav"><img src={smile} className="smile_size"/></div></Link>
            <Link to="/fav" className="size_a"><div className="smile_nav"><img src={like}className="smile_size"/></div></Link>
            <Link to="/dis" className="size_a"><div className="smile_nav"><img src={dis}className="smile_size"/></div></Link>
        </div>
        )
    }
}
export default Nav